<template>
  <div style="max-width: 100%;box-sizing: border-box" class="getFeed">
    <myHeader
      :isBack="true"
      backPath="/business"
      title="我要领料"
      subtitle="记录"
      subtitlePath="/getFeedRecord">
    </myHeader>
    <div class="seat-head"></div>
    <div class="list-block">
      <mt-cell title="批次号：" @click.native="selectBatchNum">
        {{batchNumber}}
        <span class="img"></span>
      </mt-cell>
      <mt-cell title="计划拉料日期：" @click.native="selectdrawingMaterialDate">
        <span class="color-text">
          {{drawingMaterialDateText}}
        </span>
        <span class="img"></span>
      </mt-cell>
      
      <mt-cell title="拉料司机姓名：" @click.native="selectDriver">
        {{driverName}}
        <span class="img"></span>
      </mt-cell>
      
      <mt-field
        label="司机车牌号码："
        placeholder="请输入司机车牌号码"
        :disabled="driverSlots[0].values[0].name == ''"
        v-model="driverCarID"
        class="zb-field"></mt-field>
      <mt-cell title="拉料司机身份证号：" :value="driverID"></mt-cell>
    </div>
    
    <div class="select-type">
      <div class="title">
        请选择申请类别
      </div>
      <div class="radio-select-box">
        <div class="radio-item">
          <span>领料</span>
          <img v-if="isGetFeed" @click="switchApplyHandler('isGetFeed')" src="@/assets/select-icon.png">
          <img v-else @click="switchApplyHandler('isGetFeed')" src="@/assets/noSelect-icon.png">
        </div>
        <div class="radio-item">
          <span>退料</span>
          <img v-if="isNoFeed" @click="switchApplyHandler('isNoFeed')" src="@/assets/select-icon.png">
          <img v-else @click="switchApplyHandler('isNoFeed')" src="@/assets/noSelect-icon.png">
        </div>
      </div>
    </div>
    
    <div class="apply-detail">
      <div class="top">
        <span>{{applyTitle}}</span>
      </div>
      <div class="detail-item" v-for="(item,index) in getFeedList" :key="index">
        <div class="select-detail">
          <div class="del-icon" @click="delGetFeedItem(index)">
            <img src="@/assets/del-icon.png">
          </div>
          <div class="left">
            <span class="left-name">饲料品种：</span>
            <span @click="selectFeedType(index)">{{item.feedName}}</span>
            <span class="img" @click="selectFeedType(index)"></span>
          </div>
          <div class="right">
            <span>包数：</span>
            <input type="text"
                   placeholder="请输入数量"
                   v-model="item.bclybs"
                   @input="handlerFeedNum(index,$event)">
          </div>
        </div>
        <div class="select-result">
          <div>
            <span>已领：{{item.sumReceve}}</span>
            <span>定额：{{item.quotarecevenum}}</span>
          </div>
        </div>
      </div>
      
      <div class="add-select" @click="addFeedType">
        <span class="add-icon">+</span>新增
      </div>
    </div>
    
    <SubmitBtn @click.native="submitPageData"
               :btnText="submitBtnText"
               :customStyle="myCustomStyle"></SubmitBtn>
    
    <!--计划拉料日期-->
    <mt-datetime-picker
      ref="drawingMaterialPicker"
      type="date"
      v-model="drawingMaterialDate"
      @confirm="drawingMaterialConfirm"></mt-datetime-picker>
    
    <div class="my-prop">
      <!--选择饲料类型-->
      <mt-popup
        v-model="switchSelectFeedPopup"
        position="bottom">
        <mt-picker :slots="feedTypeSlots"
                   value-key="feedName"
                   :show-toolbar="true"
                   ref="feedTypePicker">
          <span @click="handleFeedTypeConfirm" class="sure-btn">确认</span>
        </mt-picker>
      </mt-popup>
      
      <!--选择批次-->
      <mt-popup
        v-model="switchBatchNumPopup"
        position="bottom">
        <mt-picker :slots="batchNumSlots"
                   value-key="name"
                   :show-toolbar="true"
                   ref="batchPicker">
          <span @click="handleBatchConfirm" class="sure-btn">确认</span>
        </mt-picker>
      </mt-popup>
      
      <!--选择司机-->
      <mt-popup
        v-model="switchDriverPopup"
        position="bottom">
        <mt-picker :slots="driverSlots"
                   value-key="name"
                   :show-toolbar="true"
                   ref="diverPicker">
          <span @click="handleDriverConfirm" class="sure-btn">确认</span>
        </mt-picker>
      </mt-popup>
    </div>
  
  </div>
</template>

<script>
  import myHeader from '@/components/Header'
  import {formatDate, getEndDate} from '@/utils/tool'
  import SubmitBtn from "@/components/SubmitBtn";
  import {inputLimitNum, submitValidator} from '@/utils/validator'
  import {feedGetBatchNumberList, getDriverList, getFeedList, saveFeedApply} from '@/api/materialApply'
  import { Indicator } from 'mint-ui';
  import subMitBtnMixins from '@/mixins/subMitBtnMixins'
  
  export default {
    name: 'getFeed',
    mixins: [subMitBtnMixins],
    data() {
      return {
        batchNumber: '',
        batchId: '',
        switchBatchNumPopup: false,
        batchNumSlots: [{
          values: [{
            id: '',
            name: ''
          }],
          textAlign: 'center'
        }],
        drawingMaterialDateText: getEndDate(3, 'yyyy-MM-dd'),
        applyTitle: "申请领料明细",
        drawingMaterialDate: new Date(getEndDate(3, 'yyyy-MM-dd')),
        switchDriverPopup: false,
        driverSlots: [{
          values: [{
            id: '',
            name: '',
            number: '',
            phone: '',
            vehicleno: ''
          }],
          textAlign: 'center'
        }],
        driverName: '',
        driverNameId: '',
        driverID: '',
        driverCarID: '',
        selectedType: 0,
        isGetFeed: true,
        isNoFeed: false,
        switchSelectFeedPopup: false,
        getFeedList: [{
          feedName: '',
          bclybs: '',
          quotarecevenum: '',
          sumReceve: '',
          feedId: ''
        }],
        feedTypeSlots: [{
          values: [{
            feedName: "",
            feedId: '',
            quotarecevenum: '',
            sumReceve: ''
          }],
          textAlign: 'center'
        }],
        currentFeedTypeIndex: 0,
        receiveDetails: {
          feedName: '饲料名001',
          receiveNum: 2,
          receiveSinglePrice: '350元',
          receivePrice: '100元',
          alreadyGotNum: '100',
          limitNum: '300'
        },
        isShowFeedList: true
      }
    },
    components: {
      SubmitBtn,
      myHeader
    },
    created() {
      Indicator.open({
        text: '加载中...',
        spinnerType: 'fading-circle'
      });
      this._getBatchNumberList();
      this._getDriverList();
    },
    mounted() {
    
    },
    methods: {
      _getBatchNumberList() {
        feedGetBatchNumberList().then((res) => {
          if (res.data && res.data.length) {
            this.batchNumSlots[0].values = res.data.map((item, index) => {
              return {
                name: item.name,
                id: item.id
              };
            });
            this.batchNumber = this.batchNumSlots[0].values[0].name;
            this.batchId = this.batchNumSlots[0].values[0].id;
  
            this._getFeedList(this.batchNumSlots[0].values[0].id);
          } else {
            Indicator.close();
            this.isShowFeedList = false;
          }

        }, err => {
          Indicator.close();
        })
      },
      _getDriverList() {
        getDriverList().then((res) => {
          if (res.data.length) {
            this.driverSlots[0].values = res.data.map((item, index) => {
              return {
                name: item.name,
                id: item.id,
                number: item.number,
                phone: item.phone,
                vehicleno: item.vehicleno
              };
            });

            this.driverName = this.driverSlots[0].values[0].name;
            this.driverID = this.driverSlots[0].values[0].number;
            this.driverNameId = this.driverSlots[0].values[0].id;
            this.driverCarID = this.driverSlots[0].values[0].vehicleno;
          }
        });
      },
      _getFeedList(batchId) {
        getFeedList(batchId).then((res) => {
          Indicator.close();
          if (res.data && res.data.length) {
            this.feedTypeSlots[0].values = res.data.map((item, index) => {
              return {
                feedName: item.feedName,
                feedId: item.feedId,
                quotarecevenum: item.quotarecevenum,
                sumReceve: item.sumReceve
              };
            });
            const {feedName, quotarecevenum, sumReceve, feedId} = this.feedTypeSlots[0].values[0];
  
            this.getFeedList[this.currentFeedTypeIndex].feedName = feedName;
            this.getFeedList[this.currentFeedTypeIndex].quotarecevenum = quotarecevenum;
            this.getFeedList[this.currentFeedTypeIndex].sumReceve = sumReceve;
            this.getFeedList[this.currentFeedTypeIndex].feedId = feedId;
          } else {
            this.isShowFeedList = false;
            Indicator.close();
          }
          
        }).catch(err => {
          Indicator.close();
        });
      },
      selectBatchNum() {
        if (this.batchNumSlots[0].values[0].name) {
          this.switchBatchNumPopup = !this.switchBatchNumPopup;
        } else {
          this.$toast({
            message: '无可选批次，请核实业务场景！',
            duration: 1000
          });
        }
      },
      handleBatchConfirm() {
        this.batchNumber = this.$refs.batchPicker.getValues()[0].name;
        this.batchId = this.$refs.batchPicker.getValues()[0].id;
        this.switchBatchNumPopup = false;
      },
      selectDriver() {
        console.log(this.driverSlots[0].values[0].name);
        if (this.driverSlots[0].values[0] && this.driverSlots[0].values[0].name) {
          this.switchDriverPopup = !this.switchDriverPopup;
        } else {
          this.$toast({
            message: '无司机信息，请维护养户档案！',
            duration: 1000
          });
        }
      },
      handleDriverConfirm() {
        this.driverName = this.$refs.diverPicker.getValues()[0].name;
        this.driverID = this.$refs.diverPicker.getValues()[0].number;
        this.driverNameId = this.$refs.diverPicker.getValues()[0].id;
        this.driverCarID = this.$refs.diverPicker.getValues()[0].vehicleno;
        this.switchDriverPopup = false;
      },
      selectdrawingMaterialDate() {
        this.$refs.drawingMaterialPicker.open();
      },
      drawingMaterialConfirm(value) {
        this.drawingMaterialDateText = formatDate(value, 'yyyy-MM-dd');
      },
      switchApplyHandler(type) {
        if (type === 'isGetFeed') {
          if (!this.isGetFeed) {
            this.isGetFeed = true;
            this.isNoFeed = false;
            this.applyTitle = "申请领料明细";
            this.selectedType = 0;
          }
        } else if (type === 'isNoFeed') {
          if (!this.isNoFeed) {
            this.isNoFeed = true;
            this.isGetFeed = false;
            this.applyTitle = "申请退料明细";
            this.selectedType = 1;
          }
        }
      },
      handlerFeedNum(index, e) {
        this.getFeedList[index].bclybs = inputLimitNum(e);
      },
      addFeedType() {
        this.getFeedList.push({
          feedName: this.feedTypeSlots[0].values[0].feedName,
          bclybs: '',
          quotarecevenum: this.feedTypeSlots[0].values[0].quotarecevenum,
          sumReceve: this.feedTypeSlots[0].values[0].sumReceve,
          feedId: this.feedTypeSlots[0].values[0].feedId
        });
      },
      delGetFeedItem(index) {
        if (this.getFeedList.length > 1) {
          this.getFeedList = this.getFeedList.filter((item, idx) => {
            return index !== idx;
          });
        }
      },
      selectFeedType(selectedIndex) {
        if(this.isShowFeedList){
          this.currentFeedTypeIndex = selectedIndex;
          this.switchSelectFeedPopup = !this.switchSelectFeedPopup;
        }else{
          this.$toast('暂无信息！');
        }
      },
      handleFeedTypeConfirm() {
        this.getFeedList[this.currentFeedTypeIndex].feedName = this.$refs.feedTypePicker.getValues()[0].feedName;
        this.getFeedList[this.currentFeedTypeIndex].quotarecevenum = this.$refs.feedTypePicker.getValues()[0].quotarecevenum;
        this.getFeedList[this.currentFeedTypeIndex].sumReceve = this.$refs.feedTypePicker.getValues()[0].sumReceve;
        this.getFeedList[this.currentFeedTypeIndex].feedId = this.$refs.feedTypePicker.getValues()[0].feedId;
        this.switchSelectFeedPopup = false;
      },
      checkedSubmitData() {
  
        const checkList = [
          {
            val: this.batchNumber,
            rule: ["required"],
            getTipText: "批次号"
          }
        ];
        
        let checkFeedList = this.getFeedList.map((item, index) => {
          return {
            val: item.bclybs,
            rule: ['required'],
            getTipText: '饲料包数'
          }
        });
        
        let flag = submitValidator([...checkList,...checkFeedList]);
        
        return flag;
      },
      _saveFeedApply() {
        // pickDetail: [{  //领料
        //   feedName: '',  //饲料品种
        //   feedId: '',//饲料id
        //   bclybs: '', //数量（包数）
        //   sumReceve: '',  //已领
        //   quotarecevenum: '' //定额
        // }]
        
        const data = {
          batchId: this.batchId,
          batchName: this.batchNumber,
          plandate: this.drawingMaterialDateText,//计划拉料日期
          driverId: this.driverNameId,
          driverName: this.driverName,
          driverVehicleno: this.driverCarID,  //车牌号
          driverIdcard: this.driverID,  //身份证
          type: this.selectedType, //0：领料，1：退料,
          pickDetail: this.getFeedList
        };
        saveFeedApply(data).then((res) => {
          this.$toast({
            message: '提交成功！',
            duration: 1000
          });
          
          this.$router.push('/getFeedRecord');
          this.isDisabledSubmit = false;
          this.submitBtnText = '提交';
        }, err => {
          this.isDisabledSubmit = false;
          this.submitBtnText = '提交';
        });
      },
      submitPageData() {
        let flag = this.checkedSubmitData();
        if (!flag) return false;
        if (!this.isDisabledSubmit) {
          this.isDisabledSubmit = true;
          this.submitBtnText = '提交中...';
          this._saveFeedApply();
        }
        
      }
    }
  }
</script>

<style lang="scss">
  .getFeed {
    min-height: 100vh;
    background-color: #F8F8F8;
    font-size: .16rem;
    
    .head {
      background-color: #FFF;
    }
    
    .list-block {
      font-size: .16rem;
      margin-top: .1rem;
      background-color: #fff;
      
      .img {
        width: .08rem;
        height: .05rem;
        margin-left: .08rem;
        background: url('~@/assets/icon-next.png') no-repeat;
        background-size: 100%;
      }
    }
    
    .select-type {
      background-color: #fff;
      margin-top: .1rem;
      font-size: .16rem;
      
      .title {
        height: .45rem;
        padding: .1rem .15rem;
        border-bottom: 1px solid #EBEBEB;
      }
      
      .radio-select-box {
        padding-left: .15rem;
        border-bottom: 1px solid #dddddd;
        
        .radio-item {
          display: flex;
          justify-content: space-between;
          height: .52rem;
          line-height: .52rem;
          padding-left: .05rem;
          padding-right: .15rem;
          
          &:first-child {
            border-bottom: 1px solid #dddddd;
          }
          
          img {
            align-self: center;
          }
        }
      }
    }
    
    .apply-detail {
      font-size: .16rem;
      background-color: #fff;
      margin-top: .1rem;
      
      .top {
        height: .45rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 .15rem;
        border-bottom: 1px solid #EBEBEB;
        
        .del-apply {
          color: #888;
          font-size: .12rem;
        }
      }
      
      .detail-item {
        padding-left: .2rem;
        border-bottom: 1px solid #dddddd;
        
        .select-detail {
          display: flex;
          flex-wrap: nowrap;
          justify-content: space-between;
          align-items: center;
          height: .54rem;
          font-size: .14rem;
          border-bottom: 1px solid #dddddd;
          padding-right: .23rem;
          
          .left {
            display: flex;
            justify-content: space-between;
            align-items: center;
            
            .left-name {
              color: #585858;
              padding-right: .05rem;
            }
            
            .img {
              width: .1rem;
              height: .06rem;
              margin-left: .08rem;
              background: url('~@/assets/icon-next.png') no-repeat;
              background-size: 100%;
            }
          }
          
          .right {
            text-align: right;
            flex-grow: 0.5;
            
            input {
              width: .73rem;
              font-size: .14rem;
            }
          }
        }
        
        .select-result {
          font-size: .12rem;
          color: #F89B31;
          height: .37rem;
          display: flex;
          justify-content: flex-end;
          align-items: center;
          padding-right: .43rem;
          
          span:nth-child(1) {
            margin-right: .2rem;
          }
        }
      }
      
      .add-select {
        font-size: .12rem;
        color: #888;
        height: .3rem;
        display: flex;
        justify-content: center;
        align-items: center;
        border-bottom: 1px solid #EBEBEB;
        
        .add-icon {
          margin-right: .1rem;
        }
      }
    }
    
    
    .result-apply-detail {
      font-size: .16rem;
      background-color: #fff;
      margin-top: .1rem;
      
      .top {
        height: .45rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 .15rem;
        border-bottom: 1px solid #EBEBEB;
      }
      
      .detail-item {
        display: flex;
        align-items: center;
        justify-content: space-between;
        height: .54rem;
        padding: 0 .35rem;
        border-bottom: 1px solid #dddddd;
        font-size: .14rem;
        
        .item-name {
          width: 70%;
        }
        
        .item-num {
          width: 30%;
        }
      }
    }
    
    .my-prop {
      .mint-popup {
        width: 100%;
        height: 25%;
      }
      
      /deep/ .picker-toolbar {
        height: 0;
      }
      
      .sure-btn {
        position: absolute;
        top: .05rem;
        right: .05rem;
        padding: .05rem .20rem;
        font-size: .16rem;
        z-index: 99;
      }
    }
  
    /deep/ .mint-field-core {
      background: #fff;
    }
    
  }
</style>



